<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 7,  //当前表格的名称编号
        setting: null,
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        //获取通用企业信息
        this.com = this.$store.state.companyCurrent.gscompany
        //获取预设数据
        this.setting = this.$store.state.companyCurrent.content.page7
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        // //年初余额计算
        // this.fn1(2,30,[1])
        // this.fn1(4,30,[3])
        //15行计算
        this.fn2(1, 14, 15, [], 1)
        this.fn2(2, 14, 15, [], 1)
        //20行计算
        this.fn3(1)
        this.fn3(2)
        //29行计算
        this.fn2(1, 29, 30, [19, 20], 2)
        this.fn2(2, 29, 30, [19, 20], 2)
        //30行计算
        this.fn4('31_1', ['15_1', '30_1'])
        this.fn4('31_2', ['15_2', '30_2'])
        //41行计算
        this.fn2(3, 10, 11, [], 1)
        this.fn2(4, 10, 11, [], 1)
        //46行计算
        this.fn4('17_3', ['13_3', '14_3', '15_3', '16_3'])
        this.fn4('17_4', ['13_4', '14_4', '15_4', '16_4'])
        //47行计算
        this.fn4('18_3', ['11_3', '17_3'])
        this.fn4('18_4', ['11_4', '17_4'])
        //52行计算
        this.fn4('30_3', ['26_3', '27_3', '28_3', '29_3'])
        this.fn4('30_4', ['26_4', '27_4', '28_4', '29_4'])
        //53行计算
        this.fn4('31_3', ['18_3', '30_3'])
        this.fn4('31_4', ['18_4', '30_4'])
      },
      //(
      // q  要修改的值列
      // r  要修改多少行
      // seed 每行参与计算的列
      // {
      //    1加 a=a+b
      // }
      // )
      fn1(q, r, seed) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                if (this.setting[i + '_' + m]) {
                } else {
                  this.setting[i + '_' + m] = 0
                }
                sum += parseFloat(1 * (dom.val()) + 1 * (this.setting[i + '_' + m]))
              }
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      // q  要运算的列
      // r  要运算多少行
      // gps  要运算的行
      // gump 需要跳过的行
      // type 公式切换
      //  1:a=第1列从第一行起（b行+c行+d行+...）
      //  2:a=从16行开始加
      fn2(q, r, gps, jump, type) {
        var sum = 0
        for (let i = 1; i <= r; i++) {
          let dom = J('input[name=' + i + '_' + q + ']')
          if (dom.val()) {
            if (!isNaN(dom.val())) {
              if (J.inArray(i, jump) < 0) {
                if (type === 1) {
                  sum += parseFloat(dom.val())
                } else if (type === 2) {
                  if (i > 15) {
                    sum += parseFloat(dom.val())
                  }
                }
              }
            }
          }
        }
        J('input[name=' + gps + '_' + q + ']').val(sum.toFixed(2))
      },
      //18-19行
      fn3(type) {
        let dom18_1 = parseFloat(J('input[name=19_1]').val())
        let dom19_1 = parseFloat(J('input[name=20_1]').val())
        let dom18_2 = parseFloat(J('input[name=19_2]').val())
        let dom19_2 = parseFloat(J('input[name=20_2]').val())
        if (type === 1) {
          if (dom18_1 && dom19_1) {
            J('input[name=21_1]').val((dom18_1 - dom19_1).toFixed(2))
          }
        } else if (type === 2) {
          if (dom18_2 && dom19_2) {
            J('input[name=21_2]').val((dom18_2 - dom19_2).toFixed(2))
          }
        }

      },
      // q  需计算单元格的name
      // gump 参与计算的单元格name集合
      fn4(q, gump) {
        let sum = 0
        for (let i = 0; i < gump.length; i++) {
          let dom = J('input[name=' + gump[i] + ']')
          if (dom.val()) {
            sum += parseFloat(dom.val())
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

  .p2 {
    position: relative;
  }

  #zzsjmssbmxb_ycA {
    width: 400px;
    height: auto;
    position: absolute;
    top: 234px;
    left: 1px;
    background-color: #FFFED6;
    border-right: 1px #000 solid;
    display: none;
  }

  #zzsjmssbmxb_ycA div {
    border-bottom: 1px #000 solid;
    cursor: pointer;
  }

  #zzsjmssbmxb_ycB {
    display: none;
    width: 400px;
    height: auto;
    position: absolute;
    top: 483px;
    left: 0px;
    background-color: #FFFED6;
  }

  #zzsjmssbmxb_ycB div {
    cursor: pointer;
    border-bottom: 1px #000 solid;
    border-left: 1px #000 solid;
    border-right: 1px #000 solid;
  }

  .zzsjmssbmxb_ycA:hover,
  .zzsjmssbmxb_ycB:hover {
    background-color: #09f;
    color: #fff;
  }

</style>



